.wrapper{
   display: flex;
   flex-wrap: wrap;
   padding: 20px;
   margin-bottom: 20px;
   background-color: var(--components-background);
   border-bottom-left-radius: 10px;
   border-bottom-right-radius: 10px;
   width: 100%;
   min-height: 300px;
}
.city{
   margin-right: 20px;
   margin-bottom: 5px;
   padding: 10px;
   background-color: var(--card-background);
   border-radius: 10px;
   width: 200px;
   height: 270px;
}
.city__img{
   margin-left: 30px;
   margin-bottom: 5px;
}
.city__head_wrap{
   display: flex;
   justify-content: space-around;
   margin-bottom: 15px;
}
.name{
   margin-top: 10px;
   font-family: Montserrat;
   font-style: normal;
   font-weight: 500;
   font-size: 18px;
   text-align: center;
   line-height: 22px;
   color: var(--text-color);
}
.city__close{
   margin-top: 10px;
}
.city__close:hover{
   transition: 0.9s;
   transform: rotate(360deg);
   border-radius: 50%;
   box-shadow: var(--card-shadow);
}
.date{
   font-family: Montserrat;
   font-style: normal;
   font-weight: 600;
   font-size: 14px;
   line-height: 17px;
   text-align: right;
   color: #939CB0;
}
.temp__max, .temp__min, .precipitation{
   margin-left: 10px;
   margin-bottom: 5px;
   font-family: Montserrat;
   font-style: normal;
   font-weight: 500;
   font-size: 20px;
   line-height: 22px;
   color: var(--text-color);
}
.temp__min, .precipitation{
   font-size: 15px;
}

@media(max-width: 750px) {
   .wrapper{
      justify-content: center;
   }
}